<h2>1. Introduo</h2>

<p>O Termo de Abertura de Projeto busca atender a demanda proposta pela disciplina de Mtodo de Desenvolvimento de Software (MDS). O documento  detalha sobre os possveis riscos, objetivos e propostas  do ChatBot Bino para com a comunidade da FGA.</p>


<h2>2. Descrio</h2>
<p>O Bino  um bot disponibilizado no telegram, voltado para ajudar na organizao  dos estudantes da FGA, visando praticidade para gerir faltas, trabalhos, provas e outras atividades acadmicas.</p>

<h2>3. Propsito e justificativa</h2>
<p>A comunidade FGA possui uma certa dificuldade em acessar, de forma rpida e prtica, algumas informaes. O Bino surge para suprir a carncia de um mecanismo fcil e informal de acessar algumas informaes, surgindo com o propsito de disponibilizar algumas funcionalidades, como por exemplo: disponibilizar o cardpio do RU, explicar sobre o passo a passo para obter documentos acadmicos e, tambm, enviar o calendrio de matrcula. Alm disso, o Bino tambm  capaz de possibilitar com que o usurio opte por receber notificaes sobre algumas dessas informaes.</p>

<h2>4. Objetivo</h2>
<p>O principal objetivo do projeto  fornecer aos estudantes um mecanismo que facilite a forma de se controlar as faltas e de se manter organizado. Ferramentas que sem o Bino se mostram necessrias devido a uma imensa quantidade de matrias e demandas que os estudantes possuem, o bot visa economizar o tempo do aluno ao tornar o controle de faltas acessvel pelo celular bem como marcar e avisar atividades acadmicas, tudo isso sem a necessidade de instalao de um aplicativo.</p>

<h2>5. Requisitos de alto nvel</h2>
<ul>
<li>Interao entre humano e o Bot Bino de uma forma natural e interativa;</li>
<li>Fluxos de conversao prticos;</li>
<li>Construo e escolha da personalidade do bot Bino;</li>
<li>Anlise de imagens para a construo da grade horria do aluno.</li>
</ul><br/>

