<h1>Documento de Viso</h1>
<h2>1. Introduo</h2>
<p>O documento de viso define o escopo de alto nvel e o propsito do software a ser desenvolvido. Esse visa estabelecer as expectativas e reduzir os riscos do produto protegendo o cliente e os desenvolvedores do projeto.</p>
<h3>1.1 Propsito</h3>
    <p>Este documento tem como objetivo apresentar uma viso ampla sobre o ChatBot Bino deixando claro suas caractersticas, utilidades e funcionalidades.</p>
<h3>1.2 Escopo</h3>
<p>O ChatBot Bino  um projeto realizado para a disciplina Mtodos de Desenvolvimento de Software (MDS) do curso Engenharia de Software da Faculdade UnB Gama (FGA) da Universidade de Braslia (UnB).</p>
    <p>O projeto que ser realizado pelo grupo 11 composto por 6 alunos, e com a orientao do professor Jonio Marques possui como objetivo auxiliar os alunos no gerenciamento de faltas ao decorrer do semestre e lembr-los de compromissos acadmicos. A partir da interao com o ChatBot, o usurio poder cadastrar sua grade horria e compromissos acadmicos. Dessa forma o ChatBot enviar uma pergunta no horrio da aula para que o usurio marque se foi ou no a aula naquele determinado dia e enviar notificaes conforme um compromisso estiver se aproximando.</p>
