<h2>2. Posicionamento</h2>
<h3>2.1 Oportunidade de Negcio</h3>
  <p>Atualmente muitos estudantes da Universidade de Braslia no conseguem gerenciar de uma maneira efetiva o seu nmero de faltas, o que pode causar at a reprovao em certas disciplinas.</p>
   <p>Outro problema seria a falta de uma forma eficiente de organizar suas tarefas acadmicas e acabar sendo prejudicado ou pelo acmulo de matrias ou at mesmo por faltar atividades avaliativas pela falta de organizao.</p> 
   <p>O ChatBot oferecer o servio de mostrar ao usurio o seu nmero de faltas, atividades acadmicas futuras, notificar sobre o risco de faltas limite, questionar ao usurio se ele teve presena ou no no dia, notificar as atividades acadmicas a medida que elas se aproximam.</p>
<h3>2.2 Descrio do Problema</h3>
<p><b>O problema de</b>&nbsp;&nbsp;Inacessibilidade de informaes aos universitrios e demora para retirada de documentos</p>
<p><b>Afeta</b> &nbsp;&nbsp;Agilidade de atualizao</p>
<p><b>Cujo impacto </b>&nbsp;&nbsp;Falta de informao e atraso</p>
<p><b>Uma boa soluo seria</b>&nbsp;&nbsp;Automatizao de todo o processo</p>
<h3>2.3 Sentena de Posio do Produto</h3>
<p><b>Para</b>
&nbsp;&nbsp;&nbsp;&nbsp;Universidades</p>
<p><b>Que</b>&nbsp;&nbsp;Desejam organizar atividades acadmicas e controlar as faltas</p>
<p><b>O Bino</b>&nbsp;&nbsp; um ChatBot</p>
<p><b>Que</b>&nbsp;&nbsp;Visa facilitar a interao entre Universidade de Braslia e universitrios</p>
<p><b>Diferente de</b>&nbsp;&nbsp;Pginas em rede sociais</p>
<p><b>Nosso produto</b>&nbsp;&nbsp;Instrui alunos e fornece informaes em menor tempo a cerca da Universidade</p>

