<h2>4.Descrio da Soluo</h2>
<h3>4.1 Perspectiva do Produto</h3>
<p>O ChatBot Bino tem como objetivo otimizar o tempo do universitrio, aumentando a facilidade do mesmo em organizar seus horrios. Isso inclui o controle de faltas nas disciplinas nas quais o aluno est matriculado, notificando-o de acordo com o limite permitido por cada matria. Alm disso, o bot conta com um sistema de agenda, o qual o aluno pode marcar datas de provas, trabalhos, reunies e outros utilizando tambm o sistema de notificaes.</p>

